<template>
  <div class="myHome">
    <div style="display: flex;flex-direction: column;height: 80vh;padding: 20px">
      <!--面包屑-->

      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '' }">
          <span style="color: white">资源管理</span>
        </el-breadcrumb-item>
        <el-breadcrumb-item>
          <span style="color: white">上传公共服务资源</span>
        </el-breadcrumb-item>
      </el-breadcrumb>
      <div style="padding-top: 0px">
        <el-upload action="#"
                   :show-file-list="true"
                   ref="upload"
                   :before-upload="checkPackageFile"
                   :disabled="false"
                   :auto-upload="false"
                   style="display: inline-block;float: left"
                   :on-preview="handlePreview"
                   :on-success="handle_success"
                   accept=".zip"
                   :http-request="handleFileUpload"
                   :on-change="handleChange"
                   :on-remove="handleRemove"
                   limit="1"
        >
          <el-button slot="trigger" size="small" type="info" style="margin-top: 20px ">选取文件</el-button>
          <el-button size="small" style="margin-left: 20px" type="success" @click="submitUpload">推送至服务器</el-button>

        </el-upload>

      </div>
      <br>

      <div style="background-color: rgba(204,204,204,0);padding-top: 0px;flex-grow: 1"
           v-loading="maskDialogVisible"
           element-loading-text="尝试解析资源包......"
           element-loading-spinner="el-icon-loading"
           element-loading-background="rgba(0, 0, 0, 0.7)"
      >

        <div style="background-color: #0D283F; padding-top: 10px; height: 100%" v-if="infoDivVisible">
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">资源包解析结果:</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">资源包名:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.connectedPackageOriginalFileName}}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">资源包大小:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.connectedPackageSize}}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务中文名:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.softwareCnName}}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">运行环境:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.softwareEnvironment }}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务版本号:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.softwareVersion }}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务唯一标识:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.softwareUniqueName }}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">容器服务CPU需求(cpuRequests):</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.cpuRequests}}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">容器服务内存需求(memoryRequests):</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.memoryRequests }}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务开发者:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.developerName }}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">服务开发单位:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.developerGroup}}</span>
          </div>
          <div style="text-align: left;color: white;padding-left: 10px;font-size: larger">
            <span style="padding-right: 20px">联系方式:</span>
            <span style="font-weight: bold; padding-right: 10px;color: #00FFA8 ;font-size: larger">{{this.packageDetailInfo.developerPhone}}</span>
          </div>
          <br>
          <el-button @click="confirmPackage" type="success">确认提交</el-button>
          <el-button @click="cancelPackage" type="warning">取消提交</el-button>
        </div>



      </div>

    </div>
  </div>

</template>

<script>
import '../css/myCss.css'
export default {
  name: "UploadNewPackage",

  data() {
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false,

      // 选取的文件列表
      fileList:[],

      // 控制蒙层可见性
      maskDialogVisible: false,


      // 资源包详情，后端返回
      packageDetailInfo:'',


      // 确认的时候，把当前用户名传回去，用作上传者名称
      user: window.sessionStorage.getItem('userAccount'),


      infoDivVisible:false,
      confirmButtonVisible: false,
      cancelButtonVisible: false,


    };
  },
  methods: {
    handleRemove(file,fileList) {
      // 增加一个，把packageDetailInfo重置清空掉
      this.packageDetailInfo = '';
      this.$message.info("文件已移除，请重新选取");
    },


    handleChange(file, fileList) {
      this.fileList = fileList;
    },
    async submitUpload() {
      // 检查一下，文件列表为空别给后端发请求
      if (this.fileList.length == 0){
        this.$message.warning("没有检测到选取文件，请检查。");
      } else {
        const formData = new FormData();
        this.fileList.forEach(item => {
          formData.append("files",item.raw)
        })
        const config = {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        };
        //开启蒙层
        this.maskDialogVisible = true;
        // 开发环境
        await this.request.post("/Package/uploadSingle", formData,config).then(res => {
          if (res.code === "200") {
            this.packageDetailInfo = res.data;
            this.$message.success("上传成功")
            this.infoDivVisible = true;
          } else {
            this.$message.error("上传失败,原因:" + res.msg)
          }
        })
        // 部署环境
        // this.request.post("/Package/uploadSingle", formData,config).then(res => {
        //   if (res.code === "200") {
        //     this.$message.success("上传成功")
        //   } else {
        //     this.$message.error("上传失败")
        //   }
        // })

        // 关闭蒙层
        this.maskDialogVisible = false;
      }
    },

    // 资源包的确认方法，传输到后端后，将增加待审核资源的一条记录
    confirmPackage() {
      this.request.post("/Package/confirmUploadSingle",{
        params: {
          operator: this.user,
          servicePackageDetailInfoId: this.packageDetailInfo.servicePackageDetailInfoId
        }
      }).then(res => {
        if (res.code === '200') {
          this.$message.success("提交资源包成功，可在我的资源内查看相关情况");
          // 详情栏清空 、已上传文件清空； 要不直接把页面重载就行了？
          this.packageDetailInfo = '';
          this.$refs.upload.clearFiles();
        }
      })
    },


    // 取消资源包的方法，需要清空当前前端的状态，后端的已经存在包和detail也会被删除
    cancelPackage() {
      this.request.post("/Package/cancelUploadSingle",{
        params: {
          operator: this.user,
          servicePackageDetailInfoId: this.packageDetailInfo.servicePackageDetailInfoId
        }
      }).then( res => {
        if (res.code === '200') {
          this.$message.success("资源包提交已取消");
          this.packageDetailInfo = '';
          this.$refs.upload.clearFiles();
        } else {
          this.$message.error("系统错误")
        }
      })
    },

    handleFileUpload() {

    },

    checkPackageFile(file) {
      var FileExt = file.name.replace(/.+\./, "");
      if (['zip'].indexOf(FileExt.toLowerCase()) === -1) {
        this.$message({
          type: 'warning',
          message: '目前只支持zip格式的服务包封装文件'
        });
        return false;
      }
    },
    handlePreview(file) {
      console.log(file);
    },
    handle_success(res) {
      // 这个上传success的钩子函数也是说name暂时没有用，给前端页面暂存的只有UUid
      this.$message.success("文件已上传");
    },
  }
}
</script>

<style scoped>

</style>
